<!---->
<style lang='scss' rel='stylesheet/scss'>
    @import "../../utils/mixins/common";
    @import "../../utils/mixins/topSearch";

    #sys-index-container {
        @extend %content-container;
        /**/
        .avatar-uploader {
            .el-upload {
                border: 1px dashed #d9d9d9;
                border-radius: 6px;
                cursor: pointer;
                position: relative;
                overflow: hidden;

                &:hover {
                    border-color: #20a0ff;
                }
            }
        }
        .avatar-uploader-icon {
            font-size: 28px;
            color: #8c939d;
            width: 150px;
            height: 150px;
            line-height: 150px;
            text-align: center;
        }
        .avatar {
            width: 150px;
            height: 150px;
            display: block;
        } // 
        .keep {
            position: absolute;
            width: 100%;
            left: 0;
            height: 360px;
            z-index: 99;
            background: rgba(255, 255, 255, 0.7);
            top: 140px;
        }
        .synchronize {
            background: cornflowerblue;
            color: #fff;
            padding: 10px;
            line-height: 35px;
            margin-bottom: 20px;
            .el-button {
                float: right;
            }
        }
        .addForm {
            text-align: left;
            text-align-last: left;
            .img-wrap {
                display: flex;
                .image {
                    width: 200px;
                    height: 100px;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
                .button-wrap {
                    margin-left: 10px;
                }
            }
            .tag {
                span {
                    padding: 10px;
                    border: 1px solid #e2e7eb;
                    background: #fff;
                    border-right: none;
                    &:last-of-type {
                        border-right: 1px solid #e2e7eb;
                    }
                    &:hover {
                        background: #e2e7eb;
                    }
                    &.active {
                        background: #e2e7eb;
                    }
                }
            }
        }
        .dialog-footer {
            text-align: right;
            float: right;
            margin-bottom: 30px;
        }
        .manage-container {
            @extend %right-top-btnContainer;
            >* {
                color: #fff;
                border-radius: 5px;
            } // 
            .add {
                background: rgb(0, 204, 255);
            } // 
            .catmange {
                background: rgb(153, 102, 204);
            }
        }

        .search {
            @extend %top-search-container;
        } // 
        .bottom-manage {
            margin-top: 15px;
        }

        .el-dialog__wrapper {
            padding-top: 15px;
            background: rgba(0, 0, 0, .5);
            z-index: 1000;
        }
    }
</style>

<template>
    <article id="sys-index-container">
        <section class="manage-container">
            <el-button type="primary" icon="plus" @click="content.isShow = true"></el-button>
            <!--<el-button type="primary" icon="plus" @click="$router.push({ name:'section-add', params:{sys_type:'add'}})">
                <i></i>
            </el-button>-->
        </section>
        <!---->
        <ChooseContent v-model="content.isShow" v-on:result="contentConfirm"></ChooseContent>

        <!--/-->
        <el-dialog v-model="addForm" :title="formTitle">
            <section v-if="form.ref_id">
                <!--<div class="keep" v-if="form.ref_sync"></div>  -->
                <div class="synchronize">
                    {{catArr[category]}}{{category == 'course' ? form.content.course_name : form.content.title}}
                    <!--<el-button @click="form.ref_sync = 0" v-if="form.ref_sync"></el-button>
                    <el-button @click="keepSync" v-if="!form.ref_sync"></el-button>-->
                </div>
            </section>

            <!--1  -->
            <el-form label-position="top" :rules="rules">
                <el-form-item  label="">
                    <Section-category-menu :reqFun="reqFun" :placeholder="form.name" :autoClear="true" v-model="form.category_id"></Section-category-menu>
                </el-form-item>
            </el-form>
            
            <!--2  -->

            <el-form label-position="top" class="addForm" :model="form" :rules="rules" ref="form">

                <el-form-item prop="course_name" label="" :label-width="formLabelWidth">
                    <el-input v-model="form.course_name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item v-if="!form.ref_id" prop="url" label="" :label-width="formLabelWidth">
                    <el-input v-model="form.url" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="image" label="" :label-width="formLabelWidth">
                    <!---->
                    <div class="img-wrap">
                        <div v-if="form.image" class="image">
                            <img :src="form.image | fillImgPath" alt="" />
                        </div>
                        <div class="button-wrap">
                            <el-button type="primary" @click="() => {$refs.imgcropper.chooseImg()}"></el-button>
                        </div>
                    </div>
                </el-form-item>
                <el-form-item prop="description" label="" :label-width="formLabelWidth">
                    <el-input type="textarea" :rows="3" v-model="form.description" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item class="tag" label="" :label-width="formLabelWidth">
                    <span @click="toggleTag(item.value)" :class="{'active': item.value == form.tags}" v-for="(item, index) in tags">{{item.name}}</span>
                </el-form-item>
                <el-form-item prop="addate" label="" :label-width="formLabelWidth">
                    <el-date-picker v-model="form.addate" type="date" />
                </el-form-item>
          
                <el-form-item prop="sort" label="" :label-width="formLabelWidth">
                    <el-input v-model="form.sort" auto-complete="off" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addForm = false"> </el-button>
                <el-button type="primary" @click="submit('form')"> </el-button>
            </div>
        </el-dialog>


        <article class="search"> 
            <!--1 -->
  
            <!--2 --> <!--//ended-->
            <section>
                    <i></i>
                    <Section-category-menu :reqFun="reqFun" v-model="fetchParam.category_id" ></Section-category-menu>
            </section>
            <section>
                <i></i>
                <el-input v-model="fetchParam.title" placeholder="" @keyup.enter.native="fetchData" auto-complete="off"></el-input>
            </section>

        </article>

        <el-table class="data-table" v-loading="loadingData" :data="dataCache" :fit="true" @select="selectRow" @select-all="selectRow" border
            v-if="SecCateName">
            <el-table-column min-width="100" prop="category_name" label="">
            </el-table-column>
            <el-table-column min-width="100" prop="ref_sync" :formatter="format" label="">
            </el-table-column>
            <el-table-column min-width="300" prop="title" label="">
            </el-table-column>
            <el-table-column min-width="110" prop="addate" label="">
            </el-table-column>
            <el-table-column min-width="100" prop="tags" label="">
            </el-table-column>
            <el-table-column min-width="100" prop="category_rkey" label="">
            </el-table-column>
            <el-table-column fixed="right" width="180" label="">
                <template scope="scope">
                    <el-button @click="$router.push({name: 'section-edit', params: {roleInfo: scope.row, sys_id: scope.row.id}})" type="text"
                        size="small">
                    </el-button>
                    <el-button @click="$router.push({name: 'section-edit', params: {roleInfo: scope.row, sys_id: scope.row.id, sys_type:'edit'}})"
                        type="text" size="small">
                    </el-button>
                    <el-button @click="del(scope.$index, scope.row)" type="text" size="small"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination class="pagin" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="fetchParam.page"
            :page-size="fetchParam.pagesize" :page-sizes="[15, 30, 60, 100]" layout="sizes,total, prev, pager, next" :total="total">
        </el-pagination>
        <ImagEcropperInput :compress="1" :isShowBtn="false" ref="imgcropper" :confirmFn="handleImgUploaded" :aspectRatio="ratio"></ImagEcropperInput>
        <!---->
        <!--<div class="bottom-manage">
            <el-button :disabled='selectedIds.length < 1' @click="dialogTree.isShow = true"></el-button>
            <el-button :disabled='selectedIds.length < 1' @click="delMulti"></el-button>
        </div>-->
    </article>
</template>

<script>
    import dataService from '../../services/section/dataService.js'
    import cateService from '../../services/section/cateService.js'
    import ChooseContent from '../component/choose/ChooseContent'
    import courseService from '../../services/course/courseService.js'
    import SectionCategoryMenu from '../component/select/SectionCategoryMenu.vue'
    import {
        date2Str
    } from '../../utils/timeUtils.js'
    import ImagEcropperInput from '../component/upload/ImagEcropperInputSec.vue'

    function getFetchParam() {
        return {
            // status: void 0, //  1- 0-
            category_id: void 0,
            title: '',
            name: '',
            page: 1,
            pagesize: 15,
        }
    }
    function clearForm() {
        return { // 
                    id: 0,
                    title: '', // 
                    url: '', // 
                    ref_type: '', // 
                    ref_id: '', // ID
                    ref_sync: 0, // 
                    image: '', // 
                    description: '', // 
                    addate: '', // 
                    sort: '', // 
                    tags: '',
                    tags_color: '',
                    category_id: 0, //id
                }
    }
    

    export default {
        components: {
            ChooseContent,
            ImagEcropperInput,
            SectionCategoryMenu
        },
        data() {
            return {
                ratio: 0, // 
                dialog: {
                    category_id: null, //id   
                },
                drop_list: [],
                loadingData: false,
                data: [], // 
                dataCache: [],
                total: 0,
                keyWord: '',
                dialogVisible: false,
                selectedIds: [], // id
                fetchParam: getFetchParam(),
                dialogTree: {
                    isShow: false,
                    selectedId: void 0,
                },
                SecCateName: null,
                // 
                content: {
                    isShow: false
                },
                // 
                formTitle: '',
                addForm: false, // 
                formLabelWidth: '50px', // label
                catArr: {
                    'course': '',
                    'article': '',
                    'link': ''
                },
                category: 'course',
                form: clearForm(),
                tags: [{
                        name: '',
                        value: ''
                    },
                    {
                        name: '',
                        value: ''
                    },
                    {
                        name: '',
                        value: ''
                    },
                    {
                        name: '',
                        value: ''
                    }
                ],
                rules: {
                    course_name: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    url: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    category_id: [{
                        required: true,
                    }]
                },
            }
        },
        activated() {
            this.fetchData() //
            this.fetchCate() //
        },
        watch: {
            'fetchParam.category_id'(){ // 
                this.fetchData() 
            }
        },
        created() {},
        methods: {
            reqFun(param){
                return cateService.fetchData({
                    pid: 0,
                    level: -1,
                    pagesize:-1,
                })
            },
            format(row, column, cellValue) {
                return row.ref_sync == 1 ? '' : ''
            },
            //
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                //  callback 
                cb(results);
            },
            submit(form) { // 
                // this.form.section_id = this.section.currentID
                this.form.addate = this.form.addate ? date2Str(this.form.addate) : ''
                this.form.ref_type = this.form.ref_id ? this.category : 'link'
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        let reqFn = dataService.create
                        let msg = ''
                        if (this.form.id) {
                            reqFn = dataService.edit
                            msg = ''
                        }
                        // this.form.category_id = this.fetchParam.category_id

                        if (this.form.tags === '') this.form.tags_color = '#FFD220'
                        if (this.form.tags === '') this.form.tags_color = '#FF4B20'
                        if (this.form.tags === '') this.form.tags_color = '#3953C3'
                        console.log(this.form)
                        reqFn({
                            id: this.form.id,
                            category_id: this.form.category_id,
                            ref_type: this.form.ref_type,
                            ref_id: this.form.ref_id,
                            //  ref_sync : this.form.ref_sync,  
                            ref_sync: 0,
                            title: this.form.course_name,
                            image: this.form.image,
                            url: this.form.url,
                            desc: this.form.description,
                            addate: this.form.addate,
                            tags: this.form.tags,
                            tags_color: this.form.tags_color,
                            sort: this.form.sort,
                        }).then((ret) => {
                            xmview.showTip('success', msg)
                            this.addForm = false
                            this.content.isShow = false
                            // this.getSectionData(this.section.currentID)
                        }).catch((ret) => {
                            xmview.showTip('error', ret.message)
                        })
                    } else {
                        return false
                    }
                })

            },
            toggleTag(value) {
                this.form.tags = value
            },
            // 
            keepSync() {
                this.form.ref_sync = 1
                this.form.course_name = this.form.content.course_name
                this.form.image = this.form.content.image
                this.form.description = this.form.content.description
                this.form.addate = this.form.content.addate
                this.form.sort = ''
            },
            // 
            handleImgUploaded(data, ext) {
                courseService.commonUploadImageBaseSection({
                    // section_id: this.section.currentID,
                    alias: Date.now() + ext,
                    image: data
                }).then((ret) => {
                    this.form.image = ret.url
                })
            },

            // handleImgUploaded (response) {
            //     this.fetchParam.image = response.url
            // },
            addCourse(form) {
                if (this.section.loading || this.result.loading) {
                    return
                }
                this.form = clearForm()
                console.log(this.form)
                this.addForm = true
                this.formTitle = ''
            },
            //
            updateCourse(index, item) {
                this.formTitle = `${this.catArr[item.ref_type]}`
                this.form = item
                if (item.ref_type != 'link') {
                    this.category = item.ref_type
                    if (item.ref_type == 'course') {
                        this.form.content = item.course
                        this.form.title = item.course.course_name
                        this.form.description = item.description
                    } else if (item.ref_type == 'speaking') {
                        this.form.content = item.speaking
                        this.form.description = item.content
                    } else if (item.ref_type == 'article') {
                        this.form.content = item.article
                    }
                }
                this.addForm = true
            },
            //
            contentConfirm(dataObj) {
                this.form = clearForm()
                console.log(this.form)
                this.form.content = dataObj
                // this.form.category_id = dataObj.category_id  
                // this.form.ref_id = dataObj.contentid
                this.form.ref_id = dataObj.contentid
                this.form.ref_sync = 1
                this.form.tags = ''
                // this.category = dataObj.category
                if (this.form.id) delete this.form.id
                this.keepSync()
                this.addForm = true
                console.log(this.form)
            },

            //   

            getCategory_name(id) {
                let i = null
                // console.log( this.SecCateName)
                this.SecCateName.forEach(v => {
                    if (v.id == id) {
                        i = v.name
                    }
                })
                return i
            },
            //  
            fetchCate() {
                cateService.fetchData().then((ret) => {
                    this.SecCateName = ret
                    xmview.setContentLoading(false)
                })
            },
            userInfo() {
                return authUtils.getUserInfo()
            },
            initFetchParam() {
                this.fetchParam = getFetchParam()
            },
            handleCurrentChange(val) {
                this.fetchParam.page = val
                this.fetchData()
        },
            handleSizeChange(val) {dataService.fetchData
                this.fetchParam.pagesize = val
                this.fetchData()
            },
            fetchData(val) {
                return dataService.fetchData(this.fetchParam).then((ret) => {
                    this.dataCache = ret.data
                    // console.log(this.dataCache)
                    this.total=ret._exts.total
                    this.loadingData = false
                    xmview.setContentLoading(false)
                })
            },
            search(val) {
                return dataService.search(this.fetchParam).then((ret) => {})
            },
            // 
            selectRow(selection) {
                let ret = []
                selection.forEach((item) => {
                    ret.push(item.id)
                })
                this.selectedIds = ret
            },

        //     // 
            del(index, row) {
                xmview.showDialog(` <span style="color:red">${row.id}</span> ,  ?`, () => {
                    dataService.delete(row.id).then(() => {
                        this.dataCache.splice(index, 1) //
                        row.deleted = 1
                        xmview.showTip('success', '')
                    })
                })
            },
            // 
            delMulti() {
                xmview.showDialog(`?`, () => {
                    dataService.deleteMulty(this.selectedIds.join(',')).then(() => {
                        xmview.showTip('success', '')
                        this.dialogTree.isShow = false
                        setTimeout(() => {
                            this.fetchData() // 
                        }, 300)
                    })
                })
            },
        },
        computed: {
            tableData() {
                var arr = this.dataCache.filter(v => {
                    return v.title.indexOf(this.keyWord) >= 0
                })
                return arr
            },
  
            dataCache01() {
                if (this.dataCache.length > 0 && this.SecCateName) {

                    this.dataCache.forEach(v => {
                        v.formdateName = this.getCategory_name(v.category_id)
                    })
                }
                return this.dataCache    // :data="dataCache01"
            }
        }
    }
</script>


